import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';

import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';

import { Mail } from './mail.model';
import { MailService } from './mail.service';
import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';
import { fuseAnimations } from '@fuse/animations';
import { MailAccountDialogComponent } from './dialogs/account/account-form.component';
import { MatDialogRef, MatDialog, MatSnackBar } from '@angular/material';
import { Account } from '../../../../features/accounts/account.model';

import { UserService } from './user.service';

import { reject } from 'q';

@Component({
    selector   : 'fuse-mail',
    templateUrl: './mail.component.html',
    styleUrls  : ['./mail.component.scss'],
    animations : fuseAnimations
})
export class FuseMailComponent implements OnInit, OnDestroy
{
    hasSelectedMails: boolean;
    isIndeterminate: boolean;
    folders: any[];
    filters: any[];
    labels: any[];
    searchInput: FormControl;
    currentMail: Mail;

    onSelectedMailsChanged: Subscription;
    onFoldersChanged: Subscription;
    onFiltersChanged: Subscription;
    onLabelsChanged: Subscription;
    onCurrentMailChanged: Subscription;
    onSearchTextChanged: Subscription;
    onLoggedChanged: Subscription;

    dialogRef: MatDialogRef<MailAccountDialogComponent>;

    user: any;
    searchText = "";
    isLoggedIn = false;
    isSearching = false;
    isLoading = false;

    constructor(
        private mailService: MailService,
        private fuseTranslationLoader: FuseTranslationLoaderService,
        private dialog: MatDialog,

        private userService: UserService
    )
    {
        this.searchInput = new FormControl('');
        this.fuseTranslationLoader.loadTranslations(english, turkish);
    }

    ngOnInit()
    {
        this.onSelectedMailsChanged =
        this.mailService.onSelectedMailsChanged
            .subscribe(selectedMails => {

                setTimeout(() => {
                    this.hasSelectedMails = selectedMails.length > 0;
                    this.isIndeterminate = (selectedMails.length !== this.mailService.mails.length && selectedMails.length > 0);
                }, 0);
            });

        this.onFoldersChanged =
            this.mailService.onFoldersChanged
                .subscribe(folders => {
                    this.folders = this.mailService.folders;
                });

        this.onFiltersChanged =
            this.mailService.onFiltersChanged
                .subscribe(folders => {
                    this.filters = this.mailService.filters;
                });

        this.onLabelsChanged =
            this.mailService.onLabelsChanged
                .subscribe(labels => {
                    this.labels = this.mailService.labels;
                });

        this.onCurrentMailChanged =
            this.mailService.onCurrentMailChanged
                .subscribe(currentMail => {
                    if ( !currentMail )
                    {
                        this.currentMail = null;
                    }
                    else
                    {
                        this.currentMail = currentMail;
                    }
                });
        
        this.onSearchTextChanged = 
            this.mailService.onSearchTextChanged
                .subscribe(searchText =>{
                   this.searchText = searchText;
                });

        this.onLoggedChanged = 
            this.mailService.onLoggedChanged
                .subscribe(status =>{
                    this.isLoggedIn = status;
                });

        this.userService.isUserSignedIn().then((loggedIn) => {
            this.isLoggedIn = Boolean(loggedIn);
            console.log("Logged:",this.isLoggedIn);

        });
    }

    ngOnDestroy()
    {
        if (this.userService.isUserSignedIn()){

            this.onSelectedMailsChanged.unsubscribe();
            this.onFoldersChanged.unsubscribe();
            this.onFiltersChanged.unsubscribe();
            this.onLabelsChanged.unsubscribe();
            this.onCurrentMailChanged.unsubscribe();
        }

        /*
        * Temproary solution for access_token expiration issue(60mins)
        */
        // this.userService.signOut();
    }

    toggleSelectAll()
    {
        this.mailService.toggleSelectAll();
    }

    selectMails(filterParameter?, filterValue?)
    {
        this.mailService.selectMails(filterParameter, filterValue);
    }

    deselectMails()
    {
        this.mailService.deselectMails();
    }

    deSelectCurrentMail()
    {
        this.mailService.onCurrentMailChanged.next(null);
    }

    toggleLabelOnSelectedMails(labelId)
    {
        this.mailService.toggleLabelOnSelectedMails(labelId);
    }

    setFolderOnSelectedMails(folderId)
    {
        this.mailService.setFolderOnSelectedMails(folderId);
    }

    addMailAccounts()
    {
        this.dialogRef = this.dialog.open(MailAccountDialogComponent, {
            panelClass: 'mail-account-form-dialog'
        });
        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                switch ( actionType )
                {
                    /**
                     * Google
                     */
                    case 'google':

                         this.addGoogleAccount();

                        break;
                    /**
                     * Outlook
                     */
                    case 'outlook':

                        this.addOutlookAccount();

                        break;
                }
            });
    }

    
    addGoogleAccount(){
        this.userService.signIn().then(() => {
            this.isLoggedIn = true;
            this.mailService.getMailUpdate().then(()=>{});
        })
    }

    addOutlookAccount(){

    }

    searchMailsByKeyword(){
        this.isSearching = true;
        this.mailService.searchMailsByKeyword(this.searchInput.value).then(() => {
            this.isSearching = false;
        });
    }

    refreshMails(){
        this.isLoading = true;
        this.mailService.refreshMails().then(() => {
            this.isLoading = false;   
        });
    }

}
    


