import { Component, OnDestroy, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import {Observable} from 'rxjs/Rx';

import { fuseAnimations } from '@fuse/animations';

import { Mail } from '../mail.model';
import { MailService } from '../mail.service';
import { MatPaginator } from '@angular/material';
import { ViewChild } from '@angular/core'
import { FuseUtils } from '@fuse/utils';

@Component({
    selector   : 'fuse-mail-list',
    templateUrl: './mail-list.component.html',
    styleUrls  : ['./mail-list.component.scss'],
    animations : fuseAnimations
})
export class FuseMailListComponent implements OnInit, OnDestroy
{
    @ViewChild(MatPaginator) paginator: MatPaginator;

    mails: Mail[];
    currentMail: Mail;
    totalMailsSynced: Mail[];
    searchText = "";

    onMailsChanged: Subscription;
    onCurrentMailChanged: Subscription;
    onTotalCountChanged: Subscription;
    onSearchTextChanged: Subscription;
    timerSubscription: Subscription;

    totalCount = 0;
    currentPageIndex = 0; 
    pageSize = 100;


    constructor(
        private route: ActivatedRoute,
        private mailService: MailService,
        private location: Location
    )
    {
    }

    ngOnInit()
    {
  
        // Subscribe to update mails on changes
        this.onMailsChanged =
            this.mailService.onMailsChanged
                .subscribe((mails)=> {
                    this.totalMailsSynced = mails;
                    this.mails = this.totalMailsSynced.slice(this.currentPageIndex*this.pageSize,((this.currentPageIndex+1)*this.pageSize));
                    if (this.searchText !== "")
                    {
                        this.totalCount = this.totalMailsSynced.length;
                    }
                    else{
                        // Get totals mail count by Label
                        this.mailService.getTotalCount('INBOX')
                        .then((count) => {
                            this.totalCount = count;
                        });
                        if (mails && mails.length > 0)
                        {
                            console.log("SYNCED START HISTORY ID:",mails[0]);
                            console.log("SYNCED MAIL WITH START HISTORY ID:",mails[0].subject);
                            this.mailService.onStartHistoryIDChanged.next(parseInt(mails[0].historyId));
                        }
                    }
                    console.log("Total Mail Count:", this.totalMailsSynced.length);
                });
        
        // Subscribe to update current mail on changes
        this.onCurrentMailChanged =
            this.mailService.onCurrentMailChanged
                .subscribe(currentMail => {
                    if ( !currentMail )
                    {
                        // Set the current mail id to null to deselect the current mail
                        this.currentMail = null;

                        // Handle the location changes
                        const labelHandle  = this.route.snapshot.params.labelHandle,
                              filterHandle = this.route.snapshot.params.filterHandle,
                              folderHandle = this.route.snapshot.params.folderHandle;

                        if ( labelHandle )
                        {
                            this.location.go('apps/mail/label/' + labelHandle);
                        }
                        else if ( filterHandle )
                        {
                            this.location.go('apps/mail/filter/' + filterHandle);
                        }
                        else
                        {
                            this.location.go('apps/mail/' + folderHandle);
                        }
                    }
                    else
                    {
                        this.currentMail = currentMail;
                    }
                });
        
        // Subscribe to update pageindex after search text changes
        this.onSearchTextChanged = 
            this.mailService.onSearchTextChanged
                .subscribe((searchText) =>{
                    this.searchText = searchText;
                    this.currentPageIndex = 0;
                    this.paginator.firstPage();
                });

        // Get totals mail count by Label
        this.mailService.getTotalCount('INBOX')
            .then((count) => {
                this.totalCount = count;
            }).catch((err) =>{
                console.error(err);
            });  
        
        // Check latest email updates after every XX seconds 
        this.timerSubscription = Observable.interval(60*1000)
        .subscribe(() => 
            this.mailService.checkLatestEmailUpdates().then((newEmailCount) => {
                if (newEmailCount > 0) 
                {
                    this.mailService.refreshMails();
                }
            }).catch((err) => {
                console.error(err);
            })
        );
    }

    ngOnDestroy()
    {
        this.onMailsChanged.unsubscribe();
        this.onCurrentMailChanged.unsubscribe();
        if (this.timerSubscription) {
            this.timerSubscription.unsubscribe();
        } 
    }

    /**
     * Read mail
     * @param mailId
     */
    readMail(mailId)
    {
        const labelHandle  = this.route.snapshot.params.labelHandle,
              filterHandle = this.route.snapshot.params.filterHandle,
              folderHandle = this.route.snapshot.params.folderHandle;

        if ( labelHandle )
        {
            this.location.go('apps/mail/label/' + labelHandle + '/' + mailId);
        }
        else if ( filterHandle )
        {
            this.location.go('apps/mail/filter/' + filterHandle + '/' + mailId);
        }
        else
        {
            this.location.go('apps/mail/' + folderHandle + '/' + mailId);
        }

        // Set current mail
        this.mailService.setCurrentMail(mailId);
    }

    
    paginate(ev) {
        if (this.totalMailsSynced.length > ev.pageIndex * this.pageSize){
            this.mails = this.totalMailsSynced.slice(ev.pageIndex*this.pageSize,((ev.pageIndex+1)*this.pageSize));
        }
        else{
            this.mailService.getMailUpdate().then(()=>{});
        }
        this.currentPageIndex = ev.pageIndex;
    }

}



