import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material';
import { Subscription } from 'rxjs/Subscription';

import { fuseAnimations } from '@fuse/animations';

import { MailService } from '../../mail.service';
import { FuseMailComposeDialogComponent } from '../../dialogs/compose/compose.component';
import { AuthService } from '../../../../../../authentication/login/auth.service';

@Component({
    selector   : 'fuse-mail-main-sidenav',
    templateUrl: './main-sidenav.component.html',
    styleUrls  : ['./main-sidenav.component.scss'],
    animations : fuseAnimations
})
export class FuseMailMainSidenavComponent implements OnInit, OnDestroy
{
    folders: any[];
    filters: any[];
    labels: any[];
    accounts: object;
    selectedAccount: string;
    dialogRef: any;
    username: string;

    onFoldersChanged: Subscription;
    onFiltersChanged: Subscription;
    onLabelsChanged: Subscription;
    onProfileChanged: Subscription;

    constructor(
        private mailService: MailService,
        private authService: AuthService,
        public dialog: MatDialog,
    )
    {
        this.selectedAccount = 'gmail';
    }

    ngOnInit()
    {
        this.onFoldersChanged =
            this.mailService.onFoldersChanged
                .subscribe(folders => {
                    this.folders = folders;
                });

        this.onFiltersChanged =
            this.mailService.onFiltersChanged
                .subscribe(filters => {
                    this.filters = filters;
                });

        this.onLabelsChanged =
            this.mailService.onLabelsChanged
                .subscribe(labels => {
                    this.labels = labels;
                });
        this.onProfileChanged = 
            this.mailService.onProfileChanged
                .subscribe(email => {
                    this.accounts = {
                        'gmail'    : email,
                        'outlook': 'johndoe@outlook.com'
                    };

                })

        this.username = this.authService.getCurrentUser().firstName + this.authService.getCurrentUser().lastName;
    }

    ngOnDestroy()
    {
        this.onFoldersChanged.unsubscribe();
        this.onFiltersChanged.unsubscribe();
        this.onLabelsChanged.unsubscribe();
        this.onProfileChanged.unsubscribe();
    }
    
    composeDialog()
    {
        this.dialogRef = this.dialog.open(FuseMailComposeDialogComponent, {
            panelClass: 'compose-mail-dialog',
            data      : {
                action: 'new'
            }
        });
        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Send
                     */
                    case 'send':
                        this.mailService.sendMessageWithoutAttachments(formData.getRawValue()).then((response) => {
                            console.log("SENDING MAIL RESPONSE:",response);
                        }).catch((error) => {
                            console.log("SENDING MAIL ERROR", error);
                        });
                        break;
                    /**
                     * Delete
                     */
                    case 'delete':
                        console.log('send to Drafts');
                        break;
                }
            });
    }
}



