import { Component, OnDestroy, OnInit, ViewEncapsulation, Pipe, PipeTransform} from '@angular/core';
import { Subscription } from 'rxjs/Subscription';

import { fuseAnimations } from '@fuse/animations';

import { Mail } from '../mail.model';
import { MailService } from '../mail.service';

import { DomSanitizer } from '@angular/platform-browser'
import { MatDialogRef, MatDialog } from '@angular/material';
import { FuseMailComposeDialogComponent } from '../dialogs/compose/compose.component';
import { FormGroup } from '@angular/forms';

@Pipe({ name: 'safeHtml'})
export class SafeHtmlPipe implements PipeTransform  {
  constructor(private sanitized: DomSanitizer) {}
  transform(value) {
    return this.sanitized.bypassSecurityTrustHtml(value);
  }
}

@Component({
    selector   : 'fuse-mail-details',
    templateUrl: './mail-details.component.html',
    styleUrls  : ['./mail-details.component.scss'],
    animations : fuseAnimations
})
export class FuseMailDetailsComponent implements OnInit, OnDestroy
{
    mail: Mail;
    labels: any[];
    showDetails = false;

    onCurrentMailChanged: Subscription;
    onLabelsChanged: Subscription;

    dialogRef: any;
    confirmDialogRef: MatDialogRef<FuseMailComposeDialogComponent>;

    constructor(
        private mailService: MailService,
        public dialog: MatDialog
    )
    {
    }

    ngOnInit()
    {
        // Subscribe to update the current mail
        this.onCurrentMailChanged =
            this.mailService.onCurrentMailChanged
                .subscribe(currentMail => {
                    this.mail = currentMail;
                });

        // Subscribe to update on label change
        this.onLabelsChanged =
            this.mailService.onLabelsChanged
                .subscribe(labels => {
                    this.labels = labels;
                });
    }

    ngOnDestroy()
    {
        this.onCurrentMailChanged.unsubscribe();
    }

    toggleStar(event)
    {
        event.stopPropagation();

        this.mail.toggleStar();

        this.mailService.updateMail(this.mail);
    }

    toggleImportant(event)
    {
        event.stopPropagation();

        this.mail.toggleImportant();

        this.mailService.updateMail(this.mail);
    }

    replyMessage(mail){
        this.dialogRef = this.dialog.open(FuseMailComposeDialogComponent, {
            panelClass: 'compose-mail-dialog',
            data      : {
                mail: mail,
                action : 'reply'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Save
                     */
                    case 'reply':
                        this.mailService.sendMessageWithoutAttachments(formData.getRawValue(), mail).then((response) => {
                            console.log("SENDING MAIL RESPONSE:",response);
                        }).catch((error) => {
                            console.log("SENDING MAIL ERROR", error);
                        });
                        break;
                    /**
                     * Delete
                     */
                    case 'delete':

                        break;
                }
            });
    }

    forwardMessage(mail){
        this.dialogRef = this.dialog.open(FuseMailComposeDialogComponent, {
            panelClass: 'compose-mail-dialog',
            data      : {
                mail: mail,
                action : 'forward'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Save
                     */
                    case 'forward':
                        this.mailService.sendMessageWithoutAttachments(formData.getRawValue(), mail, true).then((response) => {
                            console.log("SENDING MAIL RESPONSE:",response);
                        }).catch((error) => {
                            console.log("SENDING MAIL ERROR", error);
                        });
                        break;
                    /**
                     * Delete
                     */
                    case 'delete':

                        break;
                }
            });
    }
}



