import { Component, Inject, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { MailService } from '../../mail.service';
import { Subscription } from 'rxjs/Subscription';
import { Mail } from '../../../mail-ngrx/mail.model';
import * as moment from 'moment';

@Component({
    selector     : 'fuse-mail-compose',
    templateUrl  : './compose.component.html',
    styleUrls    : ['./compose.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class FuseMailComposeDialogComponent
{
    showExtraToFields = false;
    mailForm: FormGroup;
    email: string = "";
    hasAttachements: boolean = false;
    onProfileChanged: Subscription;
    action: string;
    mail: Mail;
    dialogTitle: string;


    constructor(
        public dialogRef: MatDialogRef<FuseMailComposeDialogComponent>,
        @Inject(MAT_DIALOG_DATA) private data: any,
        private mailService: MailService
    )
    {
        this.action = data.action;

        if ( this.action === 'reply' )
        {
            this.dialogTitle = 'Reply Message';
            this.mail = data.mail;
        }
        else if ( this.action === 'forward' )
        {
            this.dialogTitle = 'Forward Message';
            this.mail = data.mail;
        }
        else 
        {
            this.dialogTitle = 'New Message';
            this.mail = new Mail({});
        }
  
        this.mailForm = this.createComposeForm();
    }

    ngOnInit()
    {
        this.onProfileChanged = this.mailService.onProfileChanged
            .subscribe((email) => {
                this.email = email;
        })
    }

    ngOnDestroy()
    {
        this.onProfileChanged.unsubscribe();
    }

    createComposeForm()
    {
        let message = "";
        if (this.action === "forward")
        {
            message = '---------- Forwarded message ----------' + 'r\n';
            message += 'From: '+ this.mail.from.email + "\r\n";
            message += 'Date: ' + moment(parseInt(this.mail.time)).format('MMMM Do YYYY, h:mm a') + "\r\n";
            message += 'Subject: ' + this.mail.subject + "\r\n";
            message += 'To: ';
            for (let _i=0; _i < this.mail.to.length; _i++ )
            {
                let to = this.mail.to[_i].email;
                message += to;
                if (_i < (this.mail.to.length - 1))
                 message += ' , ';
                
            }   
            // message += "\r\n"+"\r\n"+this.mail.message;
        }    

        return new FormGroup({
            from   : new FormControl({
                value   : this.email,
                disabled: true
            }),
            to     : new FormControl(''),
            cc     : new FormControl(''),
            bcc    : new FormControl(''),
            subject: new FormControl(this.mail.subject),
            message: new FormControl(message)
        });
    }
    

    toggleExtraToFields()
    {
        this.showExtraToFields = !this.showExtraToFields;
    }
}



